<template>
  <div>
    <div v-show="!feedback" id="chatview">
      <transition name="bounce">
        <div class="chat-container">
          <div class="chat-header">
            <img
              @click="$emit('goBack')"
              width="32"
              height="32"
              style="cursor: pointer"
              src="https://img.icons8.com/windows/32/ffffff/long-arrow-left.png"
              alt="long-arrow-left"
            />
            <h3 class="h1-chatly1345">Arthian {{ agent }}</h3>
            <NewImage v-if="agent !== null" :name="botName" size="50" />
            <img
              v-if="!ended && ended !== null"
              @click="showFeeback"
              width="40"
              height="40"
              src="https://img.icons8.com/ios/50/ffffff/delete-sign.png"
              alt="delete-sign"
            />
          </div>
          <div
            v-if="showLoader"
            class="line-loader"
            :style="{ width: loaderWidth + '%' }"
          ></div>
          <div ref="chatbody" class="chat-body" id="chat-body">
            <div v-if="isLoading" class="loading-div"></div>
            <!-- Line loader for chat timeout -->
            <!-- <div class="line-loader" :style="{ width: loaderWidth + '%' }"></div> -->

            <div class="message-box">
              <div
                v-for="(message, index) in messages"
                :key="index"
                :value="user"
                :style="[
                  !checkNxt(message, index) ? 'margin-bottom: 6px;' : '',
                ]"
                :class="[
                  message.user === user
                    ? 'message-box__item outgoing'
                    : message.user === 'user'
                    ? 'message-box__item outgoing'
                    : 'message-box__item incoming',
                ]"
              >
                <div v-if="message.type === 'text'">
                  <div v-show="checkPrev(message, index)" class="name">
                    <NewImage
                      v-if="message.user !== user"
                      :name="message.user == 'bot' ? botName : message.user"
                      :size="20"
                    />
                    {{
                      capitalizeName(
                        message.user == "bot"
                          ? botName
                          : message.user === user
                          ? "you"
                          : message.user
                      )
                    }}
                  </div>

                  <div
                    :class="[
                      message.user === user
                        ? 'user-text'
                        : message.user === 'user'
                        ? 'user-text'
                        : 'bot-text',
                    ]"
                  >
                    <span v-html="renderMessage(message.text)"></span>
                  </div>
                </div>
                <div v-else-if="message.type === 'option'">
                  <div
                    v-for="(options, index) in message.option"
                    :key="index"
                    style="margin-bottom: 5px"
                  >
                    <input
                      type="checkbox"
                      :name="`name${index}`"
                      :value="options.text"
                      :id="index"
                    />
                    <label
                      :for="index"
                      class="md-chip md-chip-clickable md-chip-hover"
                      @click="clickSelect(options)"
                    >
                      {{ options.text }}</label
                    >
                  </div>
                </div>

                <!-- if user just joined Chat -->
                <div v-if="message.type === 'joined'" class="just-joined">
                  <span class="join-message"
                    >{{ message.text }} Just joined chat</span
                  >
                </div>

                <!-- if message type is input -->

                <div v-else-if="message.type === 'input'">
                  <inputFields
                    :message="message"
                    @handleInputSubmit="handleInputSubmit"
                  />
                </div>

                <!-- if message type is voice -->
                <div v-else-if="message.type === 'voice'">
                  <!-- <audio id="audio" :src="message.text" controls></audio> -->
                  <button @click="playVoiceMessage(message)">
                    Play voice message
                  </button>
                </div>
                <div v-else-if="message.type === 'image'">
                  <img :src="message.text" alt="" width="50" height="50" />
                </div>
              </div>

              <div v-if="orderRequested" class="message-box__item outgoing">
                <inputGroup @handleOrderRequest="handleOrderRequest" />
              </div>

              <div style="text-align: left; margin-bottom: 15px">
                <div v-if="isTyping || loading" id="loading-bubble">
                  <div class="spinner">
                    <div class="bounce1"></div>
                    <div class="bounce2"></div>
                    <div class="bounce3"></div>
                  </div>
                  <!-- <div>{{ typingMsg }}</div> -->
                </div>
              </div>
            </div>
          </div>

          <div v-if="!ended">
            <div
              v-if="previewImageUrl"
              style="position: relative; width: fit-content"
            >
              <img
                :src="previewImageUrl"
                style="
                  max-width: 100%;
                  max-height: 100px;
                  min-width: 100%;
                  min-height: 100px;
                "
              />

              <button
                class="css-2chq30"
                @click="cancelImage"
                style="position: absolute; top: 5px; right: -10px"
              >
                <svg
                  width="16"
                  height="16"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M18 6L6 18M6 6L18 18"
                    stroke="currentColor"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  ></path>
                </svg>
              </button>
              <button
                @click.prevent="sendImage"
                style="position: absolute; bottom: 5px; right: -10px"
              >
                <svg
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M5 12H19M19 12L12 5M19 12L12 19"
                    stroke="currentColor"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  ></path>
                </svg>
              </button>
            </div>
            <div
              v-if="!automated"
              class="chat-footer"
              :style="[ended ? 'display: block' : '']"
            >
              <textarea
                rows="1"
                ref="message"
                :disabled="automated"
                v-model="newMessage"
                @keyup.enter="sendMessage"
                @keydown.shift.enter.prevent="insertNewLine"
                @keyup="onTyping"
                type="text"
                placeholder="Type a message..."
                style="max-height: calc(3 * (1.5em + 2px))"
              ></textarea>
              <img
                v-if="!recording"
                :src="require('@/assets/mic.png')"
                @click="startRecording"
                class="recordingIcon footIcon"
              />
              <img
                v-if="recording"
                :src="require('@/assets/stop.png')"
                @click="stopRecording"
                class="recordingIcon footIcon"
              />

              <input
                ref="fileInput"
                type="file"
                accept="image/*"
                @change="handleFileUpload"
                style="display: none"
              />
              <button
                :aria-controls="$refs.fileInput"
                @click="$refs.fileInput.click()"
                aria-label="Attach file"
                class="footIcon"
              >
                <svg
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M21.1525 10.8995L12.1369 19.9151C10.0866 21.9653 6.7625 21.9653 4.71225 19.9151C2.662 17.8648 2.662 14.5407 4.71225 12.4904L13.7279 3.47483C15.0947 2.108 17.3108 2.108 18.6776 3.47483C20.0444 4.84167 20.0444 7.05775 18.6776 8.42458L10.0156 17.0866C9.33213 17.7701 8.22409 17.7701 7.54068 17.0866C6.85726 16.4032 6.85726 15.2952 7.54068 14.6118L15.1421 7.01037"
                    stroke="currentColor"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  ></path>
                </svg>
              </button>

              <button
                class="footButton"
                :disabled="newMessage === ''"
                @click="sendMessage"
              >
                <svg
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M5 12H19M19 12L12 5M19 12L12 19"
                    stroke="currentColor"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  ></path>
                </svg>
              </button>
            </div>
          </div>

          <div
            v-else
            class="chat-footer"
            :style="[ended ? 'display: block' : '']"
          >
            <div class="text">Your conversation has ended</div>
            <button @click="$emit('createNewConvo')" class="start">
              Start Conversation
            </button>
          </div>
        </div>
      </transition>
    </div>
    <feedBack
      @getConversationBy="getConversationBy"
      @updateConversation="updateConversation"
      v-show="feedback"
      :conversationId="conversationId"
      @goBack="feedback = false"
      @goBackTo="$emit('goBackTo')"
    />
  </div>
</template>

<script>
// import incoming from "../../assets/incoming.mp3";
// import outgoing from "../../assets/outgoing.mp3";
var incoming = document.querySelector('audio[src$="incoming.mp3"]');
var outgoing = document.querySelector('audio[src$="outgoing.mp3"]');
import io from "socket.io-client";
import {
  readMessage,
  getConvoChats,
  saveChats,
  setStepConversation,
  getConversation,
} from "@/utils/firbase_utils";

import { storage } from "@/utils/firebaseDb";

import NewImage from "../../components/NewImage.vue";
import feedBack from "../../components/test/feedBack.vue";
import axios from "axios";
import { getStorage, ref, uploadBytes } from "firebase/storage";
import inputFields from "@/components/chatComp/inputFields.vue";
import classifyIntent from "@/utils/intentManager";
import inputGroup from "@/components/chatComp/inputGroup.vue";

export default {
  name: "chatView",
  data() {
    return {
      feedback: false,
      socket: null,
      automated: true,
      loading: false,
      showErrorDialog: true,
      isLoading: false,
      errorMessage: "Error Message",
      showChat: false,
      agent: null,
      orderRequested: false,
      ip: "",
      newMessage: "",
      // conversationId: "",
      isTyping: false,
      joined: false,
      typingMsg: "",
      step: 0,
      messages: [],
      chatTimeout: null,
      chatTimeoutDuration: 300000, // 5 minutes
      chatStartTime: null,
      loaderWidth: 0,
      intervalIds: [],
      showLoader: false,
      convoEnded: false,
      isBotEnabled: true,
      ended: null,
      selectedFile: null,
      trackOrderMsg: {
        text: "Sure we can helo with that <br> Please can we know this: ",
        user: "bot",
        type: "text",
      },
      flow: [
        {
          step: 0,
          next: true,
          auto: true,
          messages: [
            {
              text: "Hi! I'm glad to assist you today. Let's know each other:",
              user: "bot",
              type: "text",
            },
            {
              text: "Do you have an order already?",
              user: "bot",
              type: "text",
            },
            {
              user: "user",
              option: [
                {
                  id: 1,
                  text: "Yes",
                },
                {
                  id: 2,
                  text: "No",
                },
              ],
              type: "option",
            },
          ],
        },
        {
          step: 1.01,
          next: true,
          auto: true,
          messages: [
            {
              text: "Please can we know this: ",
              user: "bot",
              type: "text",
            },
            {
              user: "user",
              inputs: [
                {
                  id: 1.02,
                  type: "email",
                  placeholder: "email address",
                },
              ],

              type: "input",
            },
          ],
        },

        {
          step: 1.02,
          next: true,
          auto: true,
          messages: [
            {
              user: "user",
              inputs: [
                {
                  id: 1,
                  type: "number",
                  placeholder: "Order Id",
                },
              ],

              type: "input",
            },
          ],
        },

        {
          step: 1,
          next: true,
          auto: true,
          messages: [
            {
              text: "Please select what you want to do with the order",
              user: "bot",
              type: "text",
            },
            {
              user: "user",
              option: [
                {
                  id: 1.1,
                  text: "i want to make a return/swap",
                },
                {
                  id: 1.2,
                  text: "I want to know the order status",
                },
                {
                  id: 1.3,
                  text: "Others",
                },
              ],

              type: "option",
            },
          ],
        },
        {
          step: 1.1,
          next: "end",
          auto: false,
          messages: [
            {
              text: "You can easly Return or swap an order here: <a href='https://site.com/return' target='_blank'>https://site.com/return</a> ",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 1.2,
          next: "end",
          auto: false,
          messages: [
            {
              text: "You can track your order here: <a href='https://site.com/tracking' target='_blank'>https://site.com/tracking</a>",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 1.3,
          next: "end",
          auto: false,
          messages: [
            {
              text: "Tell us how else can we help?",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 2,
          next: true,
          auto: true,
          messages: [
            {
              text: "What would you like to do?",
              user: "bot",
              type: "text",
            },
            {
              user: "user",
              option: [
                {
                  id: 2.1,
                  text: "Place a new order",
                },
                {
                  id: 2.2,
                  text: "I have a question",
                },
                {
                  id: 2.3,
                  text: "Others",
                },
              ],
              type: "option",
            },
          ],
        },
        {
          step: 2.1,
          next: "end",
          auto: false,
          messages: [
            {
              text: "What products do you want?",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 2.2,
          next: "end",
          auto: false,
          messages: [
            {
              text: "You can ask any question, we are here for you",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 2.3,
          next: "end",
          auto: false,
          messages: [
            {
              text: "What other thing do you want us to do for you?",
              user: "bot",
              type: "text",
            },
          ],
        },
      ],
      newMessage: "",
      recording: false,
      mediaRecorder: null,
      previewImageUrl: null,
    };
  },
  components: {
    NewImage,
    feedBack,
    inputFields,
    inputGroup,
  },
  props: [
    "addConversation",
    "user",
    "updateConversation",
    "getConversationBy",
    "botName",
    "createNewConvo",
    "conversationId",
  ],
  methods: {
    handleFileUpload(event) {
      this.selectedFile = event.target.files[0];
      // Create a data URL for the selected image and set it as the preview image
      const reader = new FileReader();
      reader.onload = (event) => {
        this.previewImageUrl = event.target.result;
      };
      reader.readAsDataURL(this.selectedFile);
    },

    async sendImage() {
      const message = {
        conversationId: this.conversationId,
        username: this.user,
        type: "image",

        message: this.previewImageUrl,
        isUser: true,
      };

      this.socket.emit("image", message);

      const newMessage = {
        text: this.previewImageUrl,
        type: "image",
        user: this.user,
        isUser: true,
        conversationId: this.conversationId,
      };
      this.messages.push(newMessage);

      this.previewImageUrl = "";
    },

    cancelImage() {
      this.selectedFile = null;
      this.previewImageUrl = null;
    },

    insertNewLine(event) {
      if (event.shiftKey) {
        const textarea = this.$refs.message;
        const startPos = textarea.selectionStart;
        const endPos = textarea.selectionEnd;
        const currentValue = this.newMessage;
        this.newMessage = `${currentValue.substring(
          0,
          startPos
        )}\n${currentValue.substring(endPos)}`;
        // prevent the default behavior of inserting a new line
        event.preventDefault();
      }
    },

    showFeeback() {
      this.feedback = true;
    },

    async joinConversation(conversationId) {
      if (!this.ended) {
        this.socket.emit("join", { id: conversationId, user: this.user });
        this.joined = true;
      }
    },

    Processmessage() {
      let imageMessages = this.messages.filter(
        (msg) => msg.type === "text" || msg.type === "input"
      );
      let messageStrings = [];
      messageStrings = imageMessages.map((msg) => "\n" + msg.text);

      const lastMessage = imageMessages[imageMessages.length - 1].text;

      if (!lastMessage) {
        // The message is empty or contains only whitespace
        return;
      }
      let intent = classifyIntent(lastMessage);

      switch (intent) {
        case "TRACK_ORDER":
          return this.processTrackOrder();
        case "RETURN_ORDER":
        // return processReturnOrder(message);
        // Add cases for other intents and their corresponding processing logic
        default:
          return this.sendToBot();
      }
    },

    async handleOrderRequest(values) {
      const { email, orderId } = values;
      let emailMsg = {
        text: email,
        value: email,
        type: "input",
        user: this.user,
        isUser: true,
        saved: true,
        placeholder: "Email Address",
      };

      let orderIdMsg = {
        text: orderId,
        value: orderId,
        type: "input",
        user: this.user,
        isUser: true,
        saved: true,
        placeholder: "OrderId",
      };

      await this.saveMessage(this.trackOrderMsg, this.conversationId);
      await this.saveMessage(emailMsg, this.conversationId);
      await this.saveMessage(orderIdMsg, this.conversationId);

      this.newMessageSound(outgoing);

      this.messages.push(emailMsg);
      this.messages.push(orderIdMsg);
      this.orderRequested = false;

      this.isTyping = true;

      this.sendToBotIntent("order_tracking", {
        email: email,
        orderId: orderId,
      });
    },

    processTrackOrder() {
      // Step 1: Extract necessary information from the message (e.g., order ID)
      this.messages.push(this.trackOrderMsg);
      this.orderRequested = true;
    },

    async sendMessage() {
      if (this.newMessage.trim() === "" || this.orderRequested) {
        // The message is empty or contains only whitespace
        return;
      }

      const message = { text: this.newMessage };
      // const response = await axios.post('/api/messages', { conversationId: this.conversationId, message });
      await this.socket.emit("message", {
        conversationId: this.conversationId,
        type: "text",
        username: this.user,
        message: message,
        isUser: true,
      });

      this.socket.emit("typing", {
        status: false,
        user: this.user,
        id: this.conversationId,
      });
      this.chatStartTime = Date.now();
      localStorage.setItem("chatStarted", true);
      localStorage.setItem(
        `chatStartTime-${this.conversationId}`,
        this.chatStartTime
      );
      this.resetChatTimeout();
      this.newMessage = "";
    },

    onTyping() {
      if (!this.isTyping) {
        this.socket.emit("typing", {
          status: true,
          user: this.user,
          id: this.conversationId,
        });

        setTimeout(() => {
          this.socket.emit("typing", {
            status: false,
            user: this.user,
            id: this.conversationId,
          });
        }, 2000);
      }
    },

    checkPrev(object, index) {
      if (index === 0) {
        return true;
      }
      if (this.messages[index - 1].user === object.user) {
        return false;
      } else {
        return true;
      }
    },

    checkNxt(object, index) {
      if (index === 0) {
        return true;
      }
      const lastIndex = this.messages.length - 1;
      const currentIndex = this.messages.indexOf(object);
      if (currentIndex === lastIndex) {
        return true;
      }
      if (this.messages[index + 1].user === object.user) {
        return false;
      } else {
        return true;
      }
    },

    async newMessageSound(sound) {
      // var audio = new Audio(sound);

      try {
        sound.play();
      } catch (error) {
        console.log("error playing");
      }
    },

    async clickSelect(msg) {
      this.newMessageSound(outgoing);
      this.loading = true;
      this.messages.pop();
      const newMessage = {
        id: msg.id,
        text: msg.text,
        user: this.user,
        type: "text",
        isUser: true,
      };
      this.messages = [...this.messages, newMessage];

      this.loading = true;
      await this.checkCase(this.step, true, newMessage, false);
      await this.checkCase(this.step, false, newMessage, false);
      this.newMessageSound(incoming);
      this.chatToChatbottom();
      this.loading = false;
    },

    async handleInputSubmit(result) {
      const { msg, inputValue, value, id, placeholder } = result;

      this.newMessageSound(outgoing);
      this.loading = true;
      this.messages.pop();
      const updateMsg = {
        id,
        text: value,
        value: inputValue,
        type: "input",
        user: this.user,
        isUser: true,
        saved: true,
        placeholder,
      };
      // Perform actions with the input value, such as sending it to the server or updating the component's state

      this.messages = [...this.messages, updateMsg];

      // this.loading = true;
      await this.checkCase(this.step, true, updateMsg, false);
      await this.checkCase(this.step, false, updateMsg, false);
      this.newMessageSound(incoming);
      this.chatToChatbottom();
      this.loading = false;
    },

    async checkCase(step, save, msg, first) {
      // find the object that has the step give in the function value

      let valueStep = this.flow.find((obj) => obj.step === step);

      if (valueStep.next === "end" && first) {
        if (!valueStep.auto) {
          this.isSocketInConversation();
          let intervalIdReq;

          this.automated = false;
        }
        this.chatToChatbottom();
        return;
      }

      if (save) {
        valueStep.messages.pop();
        // loop through and  save user options
        for (let index = 0; index < valueStep.messages.length; index++) {
          const element = valueStep.messages[index];
          element.isUser = true;
          await this.saveMessage(element, this.conversationId);
        }

        await saveChats(msg, this.conversationId);
        await setStepConversation(this.conversationId, msg.id);
        this.step = msg.id;
      }

      // No check and update next question for automated process
      if (!save) {
        for (let index = 0; index < valueStep.messages.length; index++) {
          const element = valueStep.messages[index];
          element.isUser = true;
          if (valueStep.next === "end" || !valueStep.auto) {
            // await this.saveMessage(element, this.conversationId);
            this.Processmessage();
          } else {
            this.messages.push(element);
          }
          // this.messages.push(element);
          // if (valueStep.next === "end" || !valueStep.auto) {
          //   await this.saveMessage(element, this.conversationId);
          // }
          this.chatToChatbottom();
        }
        if (valueStep.next === "end" && !valueStep.auto) {
          await this.stopAutomate();
          // await this.Processmessage();
        }
      }
    },

    async chatToChatbottom() {
      if (
        document
          .querySelector("#chatly-app")
          .shadowRoot.querySelector("#chat-body")
      ) {
        const chatBody = document
          .querySelector("#chatly-app")
          .shadowRoot.querySelector("#chat-body");

        chatBody.scrollTop = chatBody.scrollHeight;
      }
    },

    async saveMessage(msg, id) {
      // const newMessage = {
      //   text: msg.text,
      //   type: msg.type,
      //   user: msg.user,
      //   isUser: true,
      // };
      await saveChats(msg, id);
    },

    requestAssistance() {
      if (!this.agent && !this.ended) {
        const data = { conversationId: this.conversationId, userId: this.user };
        this.socket.emit("requestAssistance", data);
        // Check if chat session has already started
        const chatStarted = localStorage.getItem("chatStarted");
        this.showLoader = true;
      }
    },

    async stopAutomate() {
      this.joinConversation(this.conversationId);
      this.automated = false;
      this.chatToChatbottom();
      this.requestAssistance();
    },

    isSocketInConversation() {
      const conversationRoom = `conversation:${this.conversationId}`;

      this.socket.emit(
        "isSocketInConversation",
        conversationRoom,
        (isJoined) => {
          if (isJoined) {
          } else {
            this.joinConversation(this.conversationId);
          }
        }
      );
    },

    async lastMessageIsBot() {
      const lastMessage = this.messages[this.messages.length - 1];

      if (lastMessage.type === "voice" || lastMessage.type === "image") {
        return true;
      }
      return lastMessage && lastMessage.user === "bot";
    },

    startChatTimeout() {
      // Calculate remaining time based on elapsed time
      const elapsedTime = Date.now() - this.chatStartTime;
      const remainingTime = Math.max(this.chatTimeoutDuration - elapsedTime, 0);

      // Start the chat timeout timer
      this.chatTimeout = setTimeout(() => {
        // End the chat session on timeout
        if (!this.ended) {
          this.socket.emit("timeUp", this.conversationId);
        }

        this.expiredConvId(true);
      }, remainingTime);

      // Update the line loader every second
      const intervalId = setInterval(() => {
        const elapsedTime = Date.now() - this.chatStartTime;
        const remainingTime = Math.max(
          this.chatTimeoutDuration - elapsedTime,
          0
        );
        const remainingPercentage =
          (remainingTime / this.chatTimeoutDuration) * 100;
        this.loaderWidth = 100 - remainingPercentage;
      }, 1000);

      this.intervalIds.push(intervalId);
    },

    resetChatTimeout() {
      // Reset the chat timeout timer
      clearTimeout(this.chatTimeout);
      if (!this.chatStartTime) {
        this.chatStartTime = Date.now();
      }
      this.startChatTimeout();

      // Clear the line loader interval
      clearInterval(this.intervalIds.pop());
      this.loaderWidth = 0;
    },

    getConversationById(conversationId) {
      const savedState = JSON.parse(localStorage.getItem("appState")) || {};
      const conversation =
        savedState.conversations?.byId[conversationId] || null;
      return conversation;
    },

    getConversationIdFromLocalStorage() {
      const conversationData = localStorage.getItem("conversationId");
      if (conversationData) {
        const conversationId = conversationData.split("-")[0];
        return conversationId;
      }
      return null; // conversation ID not found or invalid
    },

    saveConversationId(conversationId) {
      // Check if conversation ID has expired

      const timestamp = Date.now();

      const conversationData = {
        fetchState: "partial",
        read: true,
        expire: timestamp,
      };
      this.addConversation(conversationId, conversationData);

      // localStorage.setItem("conversationId", conversationData);
    },

    isConversationIdExpired() {
      // const conversationId = localStorage.getItem("conversationId");
      const convo = this.conversationId || this.conversationId;
      const expire = this.getConversationById(convo);
      if (convo && expire) {
        const conversationTimestamp = parseInt(expire.expire);
        const now = Date.now();
        const twoDaysInMilliseconds = 7 * 24 * 60 * 60 * 1000; // 2 days in milliseconds
        return now - conversationTimestamp > twoDaysInMilliseconds;
      }
      return true; // conversation ID not found or invalid
    },

    async expiredConvId(value) {
      if (this.isConversationIdExpired() || value) {
        // this.ended = true;
      }
    },

    async getConversationId() {
      let localChatId = this.conversationId || null;
      if (localChatId === null) {
        this.$router.push({ name: "home" });
      }

      return localChatId;
    },

    capitalizeName(name) {
      return name.charAt(0).toUpperCase() + name.slice(1);
    },

    iniStartTime() {
      this.chatStartTime = localStorage.getItem(
        `chatStartTime-${this.conversationId}`
      );
      if (this.chatStartTime) {
        // Restore chat start time from local storage

        this.showLoader = true;
        this.startChatTimeout();
      }
      // Set the chatStarted flag and chat start time in local storage
      if (!this.chatStartTime) {
        this.chatStartTime = Date.now();
        localStorage.setItem(
          `chatStartTime-${this.conversationId}`,
          this.chatStartTime
        );
      }
    },

    async updateConversation(conversationId, conversationData) {
      const data = JSON.parse(localStorage.getItem("appState"));
      const newState = {
        ...data,
        conversations: {
          ...data.conversations,
          byId: {
            ...data.conversations.byId,
            [conversationId]: {
              ...data.conversations.byId[conversationId],
              ...conversationData,
            },
          },
        },
      };
      //   this.updateState(newState);
      localStorage.setItem("appState", JSON.stringify(newState));
    },

    getConversationBy(conversationId) {
      const data = JSON.parse(localStorage.getItem("appState"));
      const conversations = data.conversations.byId;
      if (conversations.hasOwnProperty(conversationId)) {
        return conversations[conversationId];
      } else {
        return null;
      }
    },

    async sendToBotIntent(intent, intent_data) {
      let imageMessages = this.messages.filter(
        (msg) => msg.type === "text" || msg.type === "input"
      );
      let messageStrings = imageMessages.map((msg) => "\n" + msg.text);

      const lastMessage = this.messages[this.messages.length - 1].text;

      let allMessageText = " \nhello\n " + messageStrings.join("");
      try {
        const response = await axios({
          method: "post",
          url: "https://beta.arthian.dev/answer-me/",

          data: {
            user: "moboid",
            convo: allMessageText,
            prompt: lastMessage,
            intent: intent,
            intent_data: intent_data,
          },
        });

        await this.socket.emit("message", {
          conversationId: this.conversationId,
          type: "text",
          username: "bot",
          message: { text: response.data.response },
          isUser: false,
        });
        this.isTyping = false;
      } catch (error) {
        // console.error(error);
        this.isTyping = false;
      }
      this.sending = false;
    },

    async sendToBot() {
      let isbot = await this.lastMessageIsBot();
      if (isbot || this.automated) {
        this.sending = false;
        this.isTyping = false;
        return;
      }
      this.sending = true;
      this.isTyping = true;
      let imageMessages = this.messages.filter(
        (msg) => msg.type === "text" || msg.type === "input"
      );
      let messageStrings = imageMessages.map((msg) => "\n" + msg.text);

      const lastMessage = messageStrings[messageStrings.length - 1].text;

      let allMessageText = " \nhello\n " + messageStrings.join("");
      this.inputText = "";
      try {
        const response = await axios({
          method: "post",
          url: "https://beta.arthian.dev/answer-me/",

          data: {
            user: "moboid",
            convo: allMessageText,
            prompt: lastMessage,
            intent: false,
            intent_data: false,
          },
        });

        await this.socket.emit("message", {
          conversationId: this.conversationId,
          type: "text",
          username: "bot",
          message: { text: response.data.response },
          isUser: false,
        });
        this.isTyping = false;
      } catch (error) {
        // console.error(error);
        this.isTyping = false;
      }
      this.sending = false;
    },

    messageEndsWithGoodbye(statement) {
      if (!statement) {
        return false;
      }
      // Define common keywords and phrases that indicate a goodbye message
      const goodbyeKeywords = [
        "goodbye",
        "bye",
        "farewell",
        "see you",
        "take care",
        "have a nice",
        "later",
      ];

      // Define regular expressions to match common patterns in goodbye messages
      const patterns = [
        /\b(thanks|thank you)\b/gi,
        /\b(good night|night)\b/gi,
        /\b(talk to you|chat with you)\b/gi,
        /\b(see you|cu|ttyl|ttys)\b/gi,
        /\b(be safe)\b/gi,
        /\b(have a great|have a nice)\b/gi,
        /\b(take care)\b/gi,
        /\b(sleep well)\b/gi,
        /\b(catch you later|laters)\b/gi,
      ];

      // Check if the statement contains a goodbye keyword
      for (const keyword of goodbyeKeywords) {
        if (statement.toLowerCase().includes(keyword)) {
          return true;
        }
      }

      // Check if the statement matches any of the common patterns
      for (const pattern of patterns) {
        if (pattern.test(statement)) {
          return true;
        }
      }

      // If no keywords or patterns were found, assume it's not a goodbye message
      return false;
    },

    renderMessage(message) {
      // Detect links in the message and render them as clickable links
      const linkRegex = /(https?|ftp):\/\/[^\s]+/gi;

      if (this.isLink(message)) {
        // Replace the link in the message with an anchor tag element
        message = message.replace(
          linkRegex,
          '<a href="$&" target="_blank">$&</a>'
        );
      }

      return message;
    },

    isLink(text) {
      // Match links that do not already have an anchor tag
      const linkRegex = /^(?!.*?<a\s).*?(https?|ftp):\/\/[^\s]+.*$/i;
      return linkRegex.test(text);
    },

    async iniChat(watcher) {
      this.isLoading = true;
      let convoinfo = {};

      convoinfo = await getConversation(this.conversationId);
      this.agent = convoinfo.agent;

      this.messages = await getConvoChats(this.conversationId);

      this.step = convoinfo.step;

      await this.expiredConvId(false);

      this.chatToChatbottom();

      this.iniStartTime();
      // await this.submitText();
      this.isLoading = false;
      if (convoinfo.hasOwnProperty("ended")) {
        this.ended = convoinfo.ended;
        let item = this.getConversationBy(this.conversationId);

        item.ended = convoinfo.ended;
        await this.updateConversation(this.conversationId, item);
      } else {
        this.ended = false;
      }

      await this.checkCase(this.step, false, null, true);

      if (!this.ended && this.isBotEnabled && !this.automated && !watcher) {
        let logivVal = await this.lastMessageIsBot();

        if (!logivVal) {
          this.Processmessage("");
        }
      }
    },

    recordingAudio() {
      this.recording = true;
      navigator.mediaDevices.getUserMedia({ audio: true }).then((stream) => {
        this.mediaRecorder = new MediaRecorder(stream);
        const chunks = [];
        this.mediaRecorder.addEventListener("dataavailable", (event) => {
          chunks.push(event.data);
        });
        this.mediaRecorder.addEventListener("stop", () => {
          this.recording = false;
          const blob = new Blob(chunks, { type: "audio/webm" });
          const reader = new FileReader();
          reader.readAsDataURL(blob);
          const conversationId = this.conversationId;
          reader.addEventListener("loadend", () => {
            const message = {
              conversationId: conversationId,
              username: this.user,
              type: "voice",
              author: "Me",
              message: reader.result,
              isUser: true,
            };
            // this.socket.emit("voiceMessage", message);
            this.messages.push(message);
          });
        });
        this.mediaRecorder.start();
      });
    },

    requestMicrophonePermission() {
      navigator.mediaDevices
        .getUserMedia({ audio: true })
        .then((stream) => {
          this.mediaStream = stream;
          this.startRecording();
        })
        .catch((error) => {
          console.error(error);
        });
    },

    startRecording() {
      if (navigator.permissions) {
        navigator.permissions
          .query({ name: "microphone" })
          .then((permissionStatus) => {
            if (permissionStatus.state === "granted") {
              // permission already granted, proceed with recording
              this.recordingAudio();
            } else if (permissionStatus.state === "prompt") {
              // permission not yet granted, show permission prompt
              navigator.mediaDevices
                .getUserMedia({ audio: true })
                .then((stream) => {
                  stream.getTracks().forEach((track) => track.stop());
                  this.recordingAudio();
                })
                .catch((error) => {
                  console.error(error);
                });
            } else if (permissionStatus.state === "denied") {
              // permission denied, show error message or prompt user to enable permission
              console.error("Permission denied for microphone");
            }
          });
      }
    },

    stopRecording() {
      this.mediaRecorder.stop();
    },

    playVoiceMessage(message) {
      const audio = new Audio(message.text);
      const playButton = document.createElement("button");
      playButton.textContent = "Play";
      playButton.onclick = () => {
        audio.play();
      };
      playButton.click();
    },
  },

  async created() {
    this.socket = io("https://chatlysocket.herokuapp.com/");
    this.socket.on("connect", () => {
      this.socket.emit("userType", "customer");
    });

    // litsen to new

    this.socket.on("newMessage", async (message) => {
      if (message.user === this.user || message.user === "user") {
        await this.messages.push(message);
        await this.chatToChatbottom();
        await this.newMessageSound(outgoing);
        if (this.isBotEnabled) {
          this.Processmessage("");
        }
      } else {
        this.messages.push(message);
        this.chatToChatbottom();
        this.newMessageSound(incoming);
        readMessage(this.conversationId);
      }
    });

    this.socket.on("joined", async (message) => {
      //   this.user != message.user,
      //   this.user,
      //   message.user,
      //   "the data i need"
      // );

      if (this.user != message.user) {
        if (this.agent !== message.user) {
          this.messages.push(message);
          // this.saveMessage(message);
          await this.chatToChatbottom();
        }
      }
    });

    this.socket.on("typing", (isTyping) => {
      this.isTyping = isTyping.status;
      this.typingMsg = `${isTyping.user} is typing`;
      this.chatToChatbottom();
    });

    this.socket.on("requestAccepted", async (data) => {
      this.agent = data.userId;
    });

    await this.iniChat(false);
  },

  beforeUnmount() {
    this.socket.off();
  },

  watch: {
    messages: {
      handler(newMessages) {
        const lastMessage = newMessages[newMessages.length - 1];
        if (
          lastMessage &&
          this.messageEndsWithGoodbye(lastMessage.text) &&
          this.ended !== null &&
          !this.ended
        ) {
          setTimeout(() => {
            this.showFeeback();
          }, 3000);
        }
      },
      deep: true,
    },

    conversationId: {
      async handler(to, from) {
        await this.iniChat(true);
        // if (to.params.id !== from.params.id) {

        // }
      },
      immediate: true,
    },
  },

  computed: {
    displayAgent() {
      return this.agent || "bot";
    },
    // ...other computed properties
  },
};
</script>
