const classifyIntent = (message) => {
  const intentKeywords = {
    TRACK_ORDER: [
      "order",
      "orders",
      "product",
      "track",
      "status",
      "order status",
      "shipment",
    ],
    RETURN_ORDER: [
      "order",
      "orders",
      "product",
      "return",
      "refund",
      "exchange",
    ],
    // Add more intent categories and keywords as needed
  };

  const cleanedMessage = message.replace(/\n/g, ""); // Remove newline characters from the message
  const tokens = cleanedMessage.split(" ");

  // console.log(tokens, "tokens");

  // Perform keyword matching and context analysis
  let intentScores = {};
  for (const intent in intentKeywords) {
    intentScores[intent] = 0;

    for (const keyword of intentKeywords[intent]) {
      if (tokens.includes(keyword)) {
        intentScores[intent]++;

        // Add additional logic for contextual analysis, e.g., part-of-speech tagging
        // Adjust the score based on proximity and context of the keyword
      }
    }
  }

  // console.log(intentScores, "scores");

  // Determine the intent with the highest score
  let maxScore = -1;
  let predictedIntent = "OTHER";

  let allScoresEqual = true;
  for (const intent in intentScores) {
    if (intentScores[intent] > maxScore) {
      maxScore = intentScores[intent];
      predictedIntent = intent;
    }
    if (intentScores[intent] !== intentScores[Object.keys(intentScores)[0]]) {
      allScoresEqual = false;
    }
  }

  if (allScoresEqual) {
    return false; // All intent scores are the same
  }
  if (maxScore === 0) {
    return false; // No intent-related keywords found
  }
  return predictedIntent;
};

export default classifyIntent;
